:root {
  --mint: #3cb489;
  --mint-light: #7dd8b5;
}

.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  font-family: 'Montserrat', sans-serif;
}

.Body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Title {
  font-weight: bold;
  font-size: xx-large;
  color: var(--mint);
}

.Item {
  display: flex;
  flex-direction: column;
  padding: 12px;
}

.Item-Title {
  font-weight: bold;
}

.Item-Details {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.SubItem {
  padding-top: 6px;
  padding-left: 6px;
  padding-right: 6px;
}

a {
  color: var(--mint);
}

a:hover {
  color: var(--mint-light);
}

.Logo {
  height: 25%;
  width: 25%;
}

.Date {
  color: gray;
}